import random from './js/random';
import {createEl,appenTo} from './js/create';
const pads = document.querySelectorAll(".pads>div");
const sounds = document.querySelectorAll(".pads audio");


pads.forEach((pad, index) => {
    const randomBg = `hsl(${random(0, 359)}, 100%, 50%)`;
    pad.style.background = randomBg;
    pad.addEventListener("click", e => {
        pads.forEach(p=>p.classList.remove("active"))
        e.currentTarget.classList.add("active");
        
        sounds.forEach((s, i) => {
            s.pause();
        })
        sounds[index].currentTime = 0;
        sounds[index].play();
        createBubble(randomBg,e.currentTarget)
    })
});
const createBubble = (color,parent)=>{
    const bubble = createEl("div");
    bubble.classList.add('bubble');
    bubble.style.background = color;
    bubble.addEventListener("animationend",e=>bubble.remove())
    appenTo(parent,bubble);
    
}

